#form {
    display: flex;

    align-items: center;
    justify-content: center;
    height: 120vh;
    
}

.s {
    margin-left: 10px;
}

input[type=text] {

    padding: 8px 20px;

    box-sizing: border-box;
}


 @keyframes moveText {
     0% {
         transform: translateX(0);
     }

     50% {
         transform: translateX(200px);
         /* Adjust the distance you want the text to move */
     }

     100% {
         transform: translateX(0);
     }
 }

.flighti{
    color: rgba(0, 0, 255, 0.5);;
    background-color: rgba(0, 0, 255, 0.1);
    border-radius: 10px;
    animation: moveText 4s ease-in-out infinite
}


table th {
    padding: 10px;
    background-color: #F7F7FB;
    border-bottom: 2px solid;
    border-spacing: 0px;
    color: #8592A6;
}

table td {
    padding: 10px;
}

#k {
    margin-left: 190px;
}

table tr button {
    border: 0px;
    background-color: white;
}

button:hover {
    background-color: #5da7db
}



element.style {
    font-size: 30px;
}


#yourImageId {
    width: 25px;
    height: 20px;
    margin-left: 3px;
    margin-right: 3px;
    margin-top: 10px;
}






#date {
    display: flex;
}

#i2 {
    margin: 0px;
}

.datepicker {
    display: flex;
}

.inp {
    display: flex
}





@media screen and (max-width: 991px) {


    #date {
        flex-direction: column;
        width: min-content;

    }

    .datepicker {
        margin-bottom: 10px;
    }

    #i1,
    #i2 {
        width: 100%;
        margin-bottom: 10px;

    }
}

@media screen and (max-width:600px) {
    .sel {
        flex-direction: column;

    }

    .flight {
        text-align: center;
    }

    .s {
        margin-bottom: 5px;
        text-align: center;
        margin-left: 10px;


    }

    #d {
        width: 300px;
    }

    #yourImageId {
        margin-top: 0px;

    }

    #i1 {
        margin-bottom: 0px;
    }

    .inp {
        flex-direction: column;

        align-items: center;
        text-align: center;



    }

    #date {
        margin-left: 45px;
        justify-content: center;
        align-items: center;
    }

    .datepicker {
        flex-direction: column;
        justify-content: center;
        align-items: center;

    }

}