.testFetch {
    display: flex;
    justify-content: center;
    align-items: center;
margin-top: 210px;
flex-direction: column;
}

table th {
    padding: 10px;
   
    background-color: #F7F7FB;
    border-bottom: 2px solid;
    border-spacing: 0px;
    color: #8592A6;
}
table{
    margin-top: 30px;
}

.reser {
    color: rgba(0, 0, 255, 0.5);
    font-style: italic;
    animation: moveText 4s ease-in-out infinite;
    /* Add the animation property here */
}

@keyframes moveText {
    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(200px);
        /* Adjust the distance you want the text to move */
    }

    100% {
        transform: translateX(0);
    }
}